<template>
  <div class="top">
    <top-bar-item>
      <div slot="left" @click="back"><img src="~assets/img/common/back.svg" alt="" class="imgback"></div>
      <div slot="center" class="center">
        <span v-for="(item,index) in title"
              :key="index" @click="Color(index)" :class="{isActive:current==index}">{{item}}</span>
      </div>
    </top-bar-item>
  </div>
</template>

<script>
  import TopBarItem from 'common/TopBar/TopBarItem'
  export default {
    name: "DeatilBar",
    data() {
      return{
        title:['商品','参数','评论','推荐'],
        current: 0
      }
    },
    components: {
      TopBarItem
    },
    methods: {
      Color(index) {
        this.current = index
        this.$emit("tabclick",index)
      },
      back() {
        this.$router.back()
      }
    }
  }
</script>

<style scoped>
  .top{
    width: 100%;
    background-color: #fff;
  }
  .imgback{
    text-align: center;
    padding-top: 11px;
  }
  .center{
    position: fixed;
    color: #999;
    font-weight: normal;
    font-size: 14px;

  }
  .center span{
    flex: 1;
    margin-right: 25px;
  }
  .isActive{
    color: red;
  }
</style>