<template>
  <div v-if="Object.keys(info).length !== 0" class="info-class">
    <div class="start"></div>
    <div class="desc">{{info.desc}}</div>
    <div class="end"></div>
    <div class="key">{{info.detailImage[0].key}}</div>
    <div v-for="item in info.detailImage[0].list" class="list">
      <img :src="item" alt="" @load="imgload">
    </div>
  </div>
</template>

<script>
  export default {
    name: "DetailInfo",
    props: {
      info: {
        type:Object,
        default() {
          return {}
        }
      }
    },
    methods: {
      imgload() {
        this.$emit("imgload")
      }
    }
  }
</script>

<style scoped>
  .info-class{
    position: relative;
  }
  .start,.end{
    width: 90px;
    height: 1px;
    background-color: #a3a3a5;
    position: absolute;
  }
  .start{
    left: 12px;
    top: 10px;
  }
  .end{
    right: 10px;
    bottom: 10px;
  }
  .start:before, .end:after{
    content: '';
    position: absolute;
    width: 5px;
    height: 5px;
    background-color: #333;
  }
  .end:after{
    right: 0px;
    bottom: 0px;
  }
  .desc{
    padding-top:20px;
    padding-bottom: 20px;
    color: #333;
    font-size: 14px;
  }
  .key{
    margin: 10px;
  }
  .list{
    width: 100%;
  }
  .list img{
    width: 100%;
  }
</style>