      <del class="oldprice">{{goods.oldPrice}}</del>
      <div :style="{background:goods.color}" class="discount">{{goods.discount}}</div>
    </div>
    <div class="info">
      <span>{{goods.columns[0]}}</span>
      <span>{{goods.columns[1]}}</span>
      <span>{{goods.columns[2]}}</span>
    </div>
    <div class="custrom">
      <span v-for="item in goods.services" class="cust-span">
        <img :src="item.icon" alt="">
        <span>{{item.name}}</span>
      </span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "DetailGoods",
    props: {
      goods: {
        type: Object,
        default() {
          return {}
        }
      }
    }
  }
</script>

