  .title{
    color: #222;
    margin: 16px 8px 10px 8px;
  }
  .desc{
    margin-left: 20px;
    position: relative;
    border-bottom: 5px solid #fff;
  }
  .newprice{
    color: #FF5777;
    font-size: 22px;
  }
  .oldprice{
    color: #999;
    font-size: 12px;
    margin-left: 3px;
  }
  .discount{
    display: inline-block;
    color: #fff;
    border-radius: 5px;
    text-align: center;
    padding: 2px 3px;
