<template>
  <div id="detail">
    <detail-bar @tabclick="tabclick" ref="bar"></detail-bar>
    <scroll class="scroll" ref="scroll" @detailmove="detailmove">
      <!--轮播图-->
      <detail-swiper :TopImage="TopImage"></detail-swiper>
      <!--商品信息-->
      <detail-goods :goods="goods"></detail-goods>
      <!--商家信息-->
      <detail-shop-info :shop="shop"></detail-shop-info>
      <!--穿着效果-->
      <detail-info :info="detailInfo" @imgload="imgload"></detail-info>
      <!--商品参数-->
      <detail-param-info :paramInfo="paramInfo" ref="paraminfo"></detail-param-info>
      <!--评价-->
      <detail-comment-info :commentInfo="commentInfo" ref="commentinfo"></detail-comment-info>
      <!--热门推荐-->
      <detail-recommend :recommend="recommend"
                        ref="recommendinfo"
                        @recommendload="recommendload"></detail-recommend>
    </scroll>
    <!--回顶按钮-->
    <back-top :probetypenum="3" class="backtop" v-show="isShow" @click.native="btnTop"></back-top>
    <!--底部工具栏-->
    <bottom-bar class="bottom-bar" @addToCart="addToCart"></bottom-bar>
  </div>
</template>

<script>
  import DetailBar from './DetailChildren/DeatilBar'
  import Scroll from 'common/Scroll/Scroll'
  import DetailSwiper from './DetailChildren/DetailSwiper'
  import DetailGoods from './DetailChildren/DetailGoods'
  import DetailShopInfo from './DetailChildren/DetailShopInfo'
  import DetailInfo from './DetailChildren/DetailInfo'
  import DetailParamInfo from './DetailChildren/DetailParamInfo'
  import DetailCommentInfo from './DetailChildren/DetailCommentInfo'
  import DetailRecommend from './DetailChildren/DetailRecommend'
  import BackTop from 'content/BackTop/BackTop'
  import BottomBar from './DetailChildren/BottomBar'
  import {detailiid,Goods,Shop,GoodsParam,getrecommend} from "network/detail";

  export default {
    name: "Detail",
    data() {
      return {
        iid: null,
        TopImage:[],
        goods: {},
        shop: {},
        detailInfo: {},
        paramInfo: {},
        commentInfo: {},
        recommend: [],
        commonH: [],
        isShow: false
      }
    },
    components: {
      DetailBar,
      Scroll,
      DetailSwiper,
      DetailGoods,
      DetailShopInfo,
      DetailInfo,
      DetailParamInfo,
      DetailCommentInfo,
      DetailRecommend,
      BackTop,
      BottomBar
    },
    created() {
      /*
      要想每次获取的iid都是动态的，需要在keep-live中加exclude="Detail"
      此时组件切换过程中会重新销毁和产生
      * */
      this.iid = this.$route.params.iid
      detailiid(this.iid).then(res => {
        let data = res.data.result
        console.log(data);
        //1、轮播图数据
        this.TopImage = data.itemInfo.topImages
        //2、获取图片详情
        this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
        //3、商家信息
        this.shop = new Shop(data.shopInfo);
        //4、商品详情信息
        this.detailInfo = data.detailInfo
        //5、保存参数信息
        this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule);
        //6、保存评论信息
        if (data.rate.list) {
          this.commentInfo = data.rate.list[0];
        }
      }),
      getrecommend().then(res => {
        this.recommend = res.data.data.list;
      })

    },
    methods: {
      imgload() {
        // 图片加载完成，防抖事件
        this.$refs.scroll.refresh()
        this.commonH = []
        this.commonH.push(0)
        this.commonH.push(this.$refs.paraminfo.$el.offsetTop)
        this.commonH.push(this.$refs.commentinfo.$el.offsetTop)
        this.commonH.push(this.$refs.recommendinfo.$el.offsetTop)
        //console.log(this.commonH);0, 9215, 9947, 10160
      },
      detailmove(position) {
        for(let i = 0;i<this.commonH.length;i++) {
          if(-position.y >= this.commonH[i] && -position.y<this.commonH[i+1]){
            this.$refs.bar.current = i
            break;
          }
          if(-position.y >= this.commonH[this.commonH.length-1]){
            this.$refs.bar.current = this.commonH.length-1;
          }
        }
        this.isShow = -position.y > 1000
      },
      tabclick(index) {
        this.$refs.scroll.scrollTo(0,-this.commonH[index],100)
      },
      recommendload() {
        this.$refs.scroll.refresh()
      },
      btnTop() {
        this.$refs.scroll.scrollTo(0,0,300)
      },
      //加入购物车
      addToCart() {
        const obj = {}
        obj.image = this.TopImage[0]
        obj.title = this.goods.title
        obj.price = this.goods.oldPrice
        obj.desc = this.goods.desc
        obj.iid = this.iid
        this.$store.commit("addcar",obj)
        console.log(obj);
      }
    }
  }
</script>

<style scoped>
  #detail{
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 100;
  }
  .scroll{
    /*height: calc(100vh - 44px);
    overflow: hidden;
    position: absolute;
    top: 44px;*/
    position: absolute;
    top: 44px;
    bottom: 60px;
    overflow: hidden;
  }
  .backtop{
    position: fixed;
    right: 10px;
    bottom: 60px;
  }
</style>